body {
    background: #f3d1c2;
}
nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    background: linear-gradient(22deg, #f3d1c2 61%, #b3aa6b 62%, #b3aa6b 79%, #f3d1c2 80%);
}

nav h1 {
    padding: 10px;
    border-radius: 8px;
    color: #4C5081;
    font-size: 2rem;
    font-family: emoji, serif;
}

nav a {
    border: none;
    margin: 5px;
    font-size: 1.2rem;
    background: transparent;
    color: #4C5081;
    text-decoration: none;
}

nav a:hover {
    background-color: #4C5081;
    color: #f3d1c2;
}